import React, { useState, useEffect } from "react";
import Backdrop from "@mui/material/Backdrop";
import {
  Box,
  ThemeProvider,
  Button,
  createTheme,
  IconButton,
  TextField,
} from "@mui/material";
import EditIcon from "@mui/icons-material/Edit";

const theme = createTheme({
  palette: {
    primary: {
      main: "#007FFF",
      dark: "#0066CC",
    },
    grey: {
      50: "#fafafa",
      100: "#f5f5f5",
      200: "#eeeeee",
      300: "#e0e0e0",
      400: "#bdbdbd",
      500: "#9e9e9e",
      600: "#757575",
      700: "#616161",
      800: "#424242",
      900: "#212121",
    },
  },
});

const PublicationCountPopover = ({
  open,
  onClose,
  publicationCount,
  onSubmit,
}) => {
  const [isEditing, setIsEditing] = useState(false); // edit state
  const [editedCount, setEditedCount] = useState(publicationCount);
  // Reset edited count when publicationCount changes
  useEffect(() => {
    setEditedCount(publicationCount);
  }, [publicationCount]);

  // Prevent popover closing when in edit mode
  const handleBackdropClick = () => {
    if (!isEditing) {
      onClose();
    }
  };

  const handleEditClick = () => {
    setIsEditing(true);
  };

  const handleCancelClick = () => {
    setIsEditing(false);
    setEditedCount(publicationCount); // Reset to original count if canceled
  };

  const handleSaveClick = () => {
    onSubmit(editedCount); // Pass the updated count to the onSubmit handler
    setIsEditing(false);
  };

  return (
    <Backdrop
      sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
      open={open}
      onClick={handleBackdropClick} // Disable closing if editing
    >
      <ThemeProvider theme={theme}>
        <Box
          sx={{
            minWidth: 210,
            minHeight: 50,
            borderRadius: 1,
            bgcolor: "#6750A4",
            p: 2,
            textAlign: "center",
          }}
          onClick={(e) => e.stopPropagation()}
        >
          {isEditing ? (
            <>
              <TextField
                label="Edit Count"
                type="number"
                value={editedCount}
                onChange={(e) => setEditedCount(e.target.value)}
                fullWidth
              />
              <Button
                variant="contained"
                sx={{ mt: 2 }}
                onClick={handleSaveClick}
              >
                Save
              </Button>
              <Button variant="text" sx={{ mt: 2 }} onClick={handleCancelClick}>
                Cancel
              </Button>
            </>
          ) : (
            <>
              <p>
                Publication Count: <b>{publicationCount}</b>
              </p>
              <IconButton onClick={handleEditClick}>
                <EditIcon />
              </IconButton>
              <Button variant="contained" onClick={onSubmit}>
                Submit
              </Button>
            </>
          )}
        </Box>
      </ThemeProvider>
    </Backdrop>
  );
};

export default PublicationCountPopover;
